import api from '../../api/index'
import EditorBar from './wangeditor'
let p = document.createElement('p')
let a = document.createElement('a')
let div = document.createElement('div')
export default {
    name:'show',
    data(){
        return{
            uploadUrl:this.GLOBAL.BASE_URL+'/api/Administration/group/UploadFile',
            fileList:[],
            currentPage:1,
            pagesize:10,
            total:0,
            tableData:[],
            dialogVisible:false,//是不是因为value没在data中注册
            image:'',
            titleInfo:'',
            editor: {
                info: ''
            },
            isClear: false,
            value:'',
            imgUrl:''
        }
    },
    created(){
        this.showPublic();
    },
    methods:{
         // 上传文件
        handleSuccess(response,file, fileList){
            //上传成功要处理的事
            console.log(file.name)
                response.data[0] == ''?
                this.$message({
                    showClose: true,
                    message: '上传失败',
                    type: 'warning'
                }):
                this.$message({
                    showClose: true,
                    message: '上传成功',
                    type: 'success'
                })
            p.append(a)
            a.className = 'download'
            a.innerText = file.name;
            a.href = response.data[0];
            a.download = file.name;
            div.append(p)
            this.editor.info += div.innerHTML;
        },
        // 页面展示
        showPublic(){
            let showPolicyContent = {
                page:this.currentPage,
                rows:this.pagesize,
                type:1
            }
            api.getPublicContent(showPolicyContent).then(res=>{  
                    console.log(res)     
                    this.$message({
                        showClose: true,
                        message: res.msg,
                        type: 'warning'
                    })
                this.tableData = res.obj.rows;
                this.total = res.obj.total;
            })
        },
        // 增加跳转到富文本编辑器页面
        add(){
           this.$router.push({
                path:'/public'
            })
        },
        // 删除文件
        deleteContent(contentId){
            console.log(contentId.id)
            let id = {
                id:contentId.id
            }
            api.DeleteNotice(id).then(res=>{
                 // 图片大于2MB
               this.$message({
                    showClose: true,
                    message: res.msg,
                    type: 'success'
                })
                this.showPublic()
                console.log(res)
            })
        },
        // 编辑修改页面内容
        editorContent(id){
            let _this = this;
            this.value = id.id;
            this.dialogVisible = true;
            let getId = {
                id:this.value
            }
            api.getDetail(getId).then(res=>{
                console.log(res)
                this.editor.info = '';
                let resData = res.obj;//后台返回的数据
                _this.imgUrl = resData.pic;
                this.titleInfo = resData.title;//标题内容
                this.editor.info = resData.content;//编辑器内容
                this.image = this.GLOBAL.BASE_URL+'/'+resData.pic;
                console.log(res.obj.content)
            })
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pagesize = val;
            this.showPublic();
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currentPage = val;
             this.showPublic();
        },
        tableRowClassName({row, rowIndex}) {
            if (rowIndex === 1) {
                return 'warning-row';
            } else if (rowIndex === 3) {
                return 'success-row';
            }
            return '';
        },
        // 上传图片
         uploadPhoto(e) {
            let _this = this;
            // 利用fileReader对象获取file
            var file = e.target.files[0];
            console.log(file)
            var filesize = file.size;
            var filename = file.name;
            // 2,621,440   2M
            if (filesize > 2101440) {
                // 图片大于2MB
               this.$message({
                    showClose: true,
                    message: '图片不能超过2M',
                    type: 'warning'
                })
            }
            var reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = (e)=> {
                // 读取到的图片base64 数据编码 将此编码字符串传给后台即可
                _this.image = e.target.result;
                _this.imgUrl = e.target.result;
                  
            }
        },
        //保存数据
        saveHtml:function(event){
            let _this = this;
            console.log(this.image)
            if(this.titleInfo == '' || this.editor.info == ''){
                this.$message({
                    showClose: true,
                    message: '标题或内容不能为空',
                    type: 'warning'
                })
            }
            console.log(this.editor.info)
            let publicPage = {
                id:this.value,
                pic:_this.imgUrl,
                title:_this.titleInfo,
                content:_this.editor.info
            }
            console.log(publicPage.content)
            api.UpdateNotice(publicPage).then(res=>{
                console.log(res)
                this.$message({
                    showClose: true,
                    message: '保存成功',
                    type: 'success'
                })
                this.showPublic();
            }) 
            
            this.titleInfo = '';
            this.image = '';
            this.editor.info = '';
            this.dialogVisible = false;    
            location. reload()
        },
        change (val) {
        this.editor.info1 = val
        }
    },
    components: {
        EditorBar
    }
}



